        'COVERAGE(HXV)': '60 X 40 OR 40 X 60 HF-HORN ROTATABLE',
        "SPLmax/1m": "137dB",
        "SENSITIVITY(1W/1M)": '109dB',
        'CONNECTION': '2 X NL4 1$',
