"use client";

import { useState, useEffect } from "react";

//    
type TableRecord = {
  id: number;
  title: string;
  desc: string;
  createdAt: string;
  fileUrl: string;
  category: string;
};

//   
type Toast = {
  message: string;
  type: 'error' | 'success' | 'info';
  id: number;
};

export default function Support() {
  //   
  const [searchTerm, setSearchTerm] = useState("");
  const [currentCategory, setCurrentCategory] = useState("");
  
  //   
  const [tableData, setTableData] = useState<TableRecord[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  
  //  
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 10; //    
  
  //   
  const [toasts, setToasts] = useState<Toast[]>([]);
  
  // Google Sheets ID
  const SHEET_ID = "1TnHBUzm-Pefue-B-WOS363wcblYZJY3WLnRY5DG4PIc";
  
  //    
  const addToast = (message: string, type: 'error' | 'success' | 'info' = 'info') => {
    const id = Date.now();
    setToasts((prev) => [...prev, { message, type, id }]);
    
    // 3    
    setTimeout(() => {
      setToasts((prev) => prev.filter((toast) => toast.id !== id));
    }, 3000);
  };
  
  // Google Sheets  
  useEffect(() => {
    const fetchGoogleSheetsData = async () => {
      try {
        setLoading(true);
        setError(null);
        
        // Google Sheets API URL ( )
        const url = `https://docs.google.com/spreadsheets/d/${SHEET_ID}/gviz/tq?tqx=out:json`;
        
        const response = await fetch(url);
        if (!response.ok) {
          throw new Error('  .');
        }
        
        // Google Sheets API JSON     .
        //     .
        const text = await response.text();
        
        //  "/*O_o*/\ngoogle.visualization.Query.setResponse({...});" .
        // JSON  .
        const jsonText = text.substring(text.indexOf('{'), text.lastIndexOf('}') + 1);
        const data = JSON.parse(jsonText);
        
        //   (table.rows table.cols   )
        if (data.table && data.table.rows && data.table.cols) {
          const parsedRecords: TableRecord[] = data.table.rows.map((row: any, index: number) => {
            const values = row.c;
            
            //    (null )
            const getValue = (idx: number) => {
              return values[idx] && values[idx].v ? values[idx].v : '';
            };
            
            // Google Drive  URL ID   URL 
            const fileUrl = getValue(4);
            let downloadUrl = '';
            
            if (fileUrl) {
              //   Google Drive URL ID 
              let fileId = '';
              
              //  1: https://drive.google.com/file/d/FILE_ID/view
              if (fileUrl.includes('/file/d/')) {
                const match = fileUrl.match(/\/file\/d\/([^\/]+)/);
                if (match && match[1]) fileId = match[1];
              } 
              //  2: https://drive.google.com/open?id=FILE_ID
              else if (fileUrl.includes('?id=')) {
                const match = fileUrl.match(/[?&]id=([^&]+)/);
                if (match && match[1]) fileId = match[1];
              }
              //  3:   ID  
              else if (!fileUrl.includes('http')) {
                fileId = fileUrl.trim();
              }
              //   ,  URL 
              else {
                downloadUrl = fileUrl;
              }
              
              //  ID   URL 
              if (fileId) {
                downloadUrl = `https://drive.google.com/uc?export=download&id=${fileId}`;
              }
            }
            
            //   
            const formatDate = (dateString: string): string => {
              if (!dateString) return '';
              
              // "Date(YYYY,MM,DD)"  
              const datePattern = /Date\((\d+),\s*(\d+),\s*(\d+)\)/;
              const dateMatch = String(dateString).match(datePattern);
              
              if (dateMatch) {
                // Date(YYYY,MM,DD)  
                const year = parseInt(dateMatch[1]);
                const month = parseInt(dateMatch[2]) + 1; // JavaScript Date  0  +1
                const day = parseInt(dateMatch[3]);
                
                // YYYY-MM-DD  
                return `${year}-${String(month).padStart(2, '0')}-${String(day).padStart(2, '0')}`;
              }
              
              //    
              try {
                // ISO (: 2023-01-01T00:00:00Z)     
                const date = new Date(dateString);
                
                //   
                if (isNaN(date.getTime())) {
                  return dateString; //      
                }
                
                // YYYY-MM-DD  
                const year = date.getFullYear();
                const month = String(date.getMonth() + 1).padStart(2, '0');
                const day = String(date.getDate()).padStart(2, '0');
                
                return `${year}-${month}-${day}`;
              } catch (e) {
                console.error('  :', dateString);
                return dateString; //      
              }
            };
            
            return {
              id: getValue(0) || index + 1,
              title: getValue(1) || '',
              desc: getValue(2) || '',
              createdAt: formatDate(getValue(3)), //   
              fileUrl: downloadUrl, //   URL 
              category: getValue(5) || ''
            };
          }).filter((record: TableRecord) => record.title); //   
          
          setTableData(parsedRecords);
          
          if (parsedRecords.length === 0) {
            addToast(' .', 'info');
          }
        } else {
          //     
          throw new Error('   .');
        }
      } catch (err) {
        console.error('  :', err);
        setError('    .');
        setTableData([]); //      
        
        //   
        const errorMessage = err instanceof Error ? err.message : '    .';
        addToast(errorMessage, 'error');
      } finally {
        setLoading(false);
      }
    };
    
    fetchGoogleSheetsData();
  }, []);
  
  //       
  const availableCategories = ["", ...Array.from(new Set(tableData.map(item => item.category)))];
  
  //      
  const filteredData = tableData.filter(item => 
    (currentCategory === "" || item.category === currentCategory) &&
    (item.title.toLowerCase().includes(searchTerm.toLowerCase()) || 
     item.desc.toLowerCase().includes(searchTerm.toLowerCase()))
  );
  
  //    
  const totalPages = Math.ceil(filteredData.length / itemsPerPage);
  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentItems = filteredData.slice(indexOfFirstItem, indexOfLastItem);
  
  //   
  const handlePageChange = (pageNumber: number) => {
    setCurrentPage(pageNumber);
  };
  
  //   
  const handleSearchChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setSearchTerm(e.target.value);
    setCurrentPage(1); //     
  };
  
  //   
  const handleCategoryChange = (category: string) => {
    setCurrentCategory(category);
    setCurrentPage(1); //      
  };

  //   
  const getCategoryColor = (category: string) => {
    switch(category) {
      case "":
        return "bg-indigo-900 text-indigo-200";
      case "":
        return "bg-emerald-900 text-emerald-200";
      case "":
        return "bg-amber-900 text-amber-200";
      case "":
        return "bg-purple-900 text-purple-200";
      case "":
        return "bg-slate-800 text-slate-200";
      default:
        return "bg-blue-900 text-blue-200";
    }
  };

  return (
    <div className="bg-gray-900 max-w-6xl mx-auto py-20 px-4 sm:px-6 lg:px-8 relative">
      <h1 className="text-4xl font-bold text-center mb-8 text-white"></h1>
      <p className="text-center text-lg text-gray-400 mb-12">
              .
      </p>
      
      {/*    */}
      <div className="fixed top-8 right-8 z-50 space-y-2">
        {toasts.map((toast) => (
          <div 
            key={toast.id}
            className={`px-4 py-3 rounded-lg shadow-lg text-white flex items-center space-x-2 transition-all duration-300 ${
              toast.type === 'error' ? 'bg-red-600' : 
              toast.type === 'success' ? 'bg-green-600' : 
              'bg-blue-600'
            }`}
          >
            <div>
              {toast.type === 'error' && (
                <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                  <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
                </svg>
              )}
              {toast.type === 'success' && (
                <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                  <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                </svg>
              )}
              {toast.type === 'info' && (
                <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                  <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm1-11a1 1 0 10-2 0v2H7a1 1 0 100 2h2v2a1 1 0 102 0v-2h2a1 1 0 100-2h-2V7z" clipRule="evenodd" />
                </svg>
              )}
            </div>
            <p>{toast.message}</p>
          </div>
        ))}
      </div>
      
      {/*    UI */}
      <div className="flex flex-col sm:flex-row justify-between items-center mb-8 space-y-4 sm:space-y-0">
        <div className="flex space-x-2 overflow-x-auto pb-2">
          {availableCategories.map(category => (
            <button 
              key={category}
              onClick={() => handleCategoryChange(category)} 
              className={`px-4 py-2 rounded-full text-sm font-medium transition-all duration-200 ${
                currentCategory === category 
                ? "bg-gray-100 text-gray-900 shadow-md" 
                : "bg-gray-800 text-gray-300 hover:bg-gray-700"
              }`}
            >
              {category}
            </button>
          ))}
        </div>
        
        <div className="relative w-full sm:w-64">
          <input
            type="text"
            placeholder="..."
            value={searchTerm}
            onChange={handleSearchChange}
            className="w-full px-4 py-2 pl-10 border border-gray-700 bg-gray-800 rounded-full text-gray-200 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all duration-200 placeholder-gray-500"
          />
          <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 absolute left-3 top-2.5 text-gray-500" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
          </svg>
        </div>
      </div>
      
      {/*   */}
      <div className="bg-gray-800 shadow-lg rounded-xl overflow-hidden border border-gray-700">
        {loading ? (
          <div className="py-16 text-center text-gray-400">
            <svg className="animate-spin h-12 w-12 mx-auto mb-4 text-blue-400" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
              <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
              <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
            </svg>
            <h3 className="text-lg font-medium mb-1 text-gray-300">  ...</h3>
            <p className="text-sm text-gray-500"> </p>
          </div>
        ) : tableData.length === 0 ? (
          <div className="py-16 text-center text-gray-400">
            <svg xmlns="http://www.w3.org/2000/svg" className="h-16 w-16 mx-auto text-gray-600 mb-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="1.5" d="M20 13V6a2 2 0 00-2-2H6a2 2 0 00-2 2v7m16 0v5a2 2 0 01-2 2H6a2 2 0 01-2-2v-5m16 0h-2.586a1 1 0 00-.707.293l-2.414 2.414a1 1 0 01-.707.293h-3.172a1 1 0 01-.707-.293l-2.414-2.414A1 1 0 006.586 13H4" />
            </svg>
            <h3 className="text-lg font-medium mb-1 text-gray-300">  </h3>
            <p className="text-sm text-gray-500">  </p>
          </div>
        ) : filteredData.length === 0 ? (
          <div className="py-16 text-center text-gray-400">
            <svg xmlns="http://www.w3.org/2000/svg" className="h-16 w-16 mx-auto text-gray-600 mb-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="1.5" d="M9.172 16.172a4 4 0 015.656 0M9 10h.01M15 10h.01M12 2a10 10 0 110 20 10 10 0 010-20z" />
            </svg>
            <h3 className="text-lg font-medium mb-1 text-gray-300">  </h3>
            <p className="text-sm text-gray-500">   </p>
          </div>
        ) : (
          <>
            {/*    */}
            <div className="hidden lg:block overflow-x-auto">
              <table className="min-w-full divide-y divide-gray-700">
                <thead>
                  <tr className="bg-gray-900">
                    <th scope="col" className="px-6 py-4 text-left text-xs font-semibold text-gray-400 uppercase tracking-wider"></th>
                    <th scope="col" className="px-6 py-4 text-left text-xs font-semibold text-gray-400 uppercase tracking-wider"></th>
                    <th scope="col" className="px-6 py-4 text-left text-xs font-semibold text-gray-400 uppercase tracking-wider"></th>
                    <th scope="col" className="px-6 py-4 text-left text-xs font-semibold text-gray-400 uppercase tracking-wider"></th>
                    <th scope="col" className="px-6 py-4 text-left text-xs font-semibold text-gray-400 uppercase tracking-wider"></th>
                    <th scope="col" className="px-6 py-4 text-left text-xs font-semibold text-gray-400 uppercase tracking-wider"></th>
                  </tr>
                </thead>
                <tbody className="bg-gray-800 divide-y divide-gray-700">
                  {currentItems.map((item, index) => (
                    <tr key={item.id} className="hover:bg-gray-700 transition-colors duration-150">
                      <td className="px-6 py-5 whitespace-nowrap">
                        <div className="text-sm font-medium text-gray-300">{item.id}</div>
                      </td>
                      <td className="px-6 py-5 whitespace-nowrap">
                        <div className="text-sm font-medium text-gray-200">{item.title}</div>
                      </td>
                      <td className="px-6 py-5">
                        <div className="text-sm text-gray-400 max-w-xs truncate">{item.desc}</div>
                      </td>
                      <td className="px-6 py-5 whitespace-nowrap">
                        <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${getCategoryColor(item.category)}`}>
                          {item.category}
                        </span>
                      </td>
                      <td className="px-6 py-5 whitespace-nowrap">
                        <div className="text-sm text-gray-400">{item.createdAt}</div>
                      </td>
                      <td className="px-6 py-5 whitespace-nowrap">
                        <div className="flex items-center">
                          {item.fileUrl ? (
                            <a
                              href={item.fileUrl}
                              className="inline-flex items-center px-3 py-1.5 border border-gray-600 rounded-full text-xs font-medium bg-gray-700 text-blue-300 hover:bg-gray-600 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-offset-gray-800 focus:ring-blue-500 transition-colors duration-200"
                              download
                              target="_blank"
                              rel="noopener noreferrer"
                            >
                              <svg className="w-4 h-4 mr-1.5" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4 16v1a3 3 0 003 3h10a3 3 0 003-3v-1m-4-4l-4 4m0 0l-4-4m4 4V4" />
                              </svg>
                              
                            </a>
                          ) : (
                            <span className="text-sm text-gray-500"> </span>
                          )}
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>

            {/*    */}
            <div className="lg:hidden space-y-4 p-4">
              {currentItems.map((item, index) => (
                <div key={item.id} className="bg-gray-700 rounded-lg p-4 border border-gray-600 hover:bg-gray-600 transition-colors duration-150">
                  {/*  */}
                  <div className="flex justify-between items-start mb-3">
                    <div className="flex items-center space-x-2">
                      <span className="text-xs font-medium text-gray-400 bg-gray-600 px-2 py-1 rounded">
                        #{item.id}
                      </span>
                      <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium ${getCategoryColor(item.category)}`}>
                        {item.category}
                      </span>
                    </div>
                    <span className="text-xs text-gray-400">{item.createdAt}</span>
                  </div>
                  
                  {/*  */}
                  <h3 className="text-sm font-semibold text-gray-200 mb-2 line-clamp-2">
                    {item.title}
                  </h3>
                  
                  {/*  */}
                  <p className="text-xs text-gray-400 mb-4 line-clamp-3">
                    {item.desc}
                  </p>
                  
                  {/*   */}
                  <div className="flex justify-end">
                    {item.fileUrl ? (
                      <a
                        href={item.fileUrl}
                        className="inline-flex items-center px-3 py-2 border border-gray-500 rounded-lg text-xs font-medium bg-gray-600 text-blue-300 hover:bg-gray-500 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-offset-gray-700 focus:ring-blue-500 transition-colors duration-200"
                        download
                        target="_blank"
                        rel="noopener noreferrer"
                      >
                        <svg className="w-4 h-4 mr-1.5" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4 16v1a3 3 0 003 3h10a3 3 0 003-3v-1m-4-4l-4 4m0 0l-4-4m4 4V4" />
                        </svg>
                        
                      </a>
                    ) : (
                      <span className="text-xs text-gray-500"> </span>
                    )}
                  </div>
                </div>
              ))}
            </div>
          </>
        )}
      </div>
      
      {/*  UI */}
      {totalPages > 0 && (
        <div className="flex justify-center mt-8">
          <nav className="flex items-center space-x-1">
            <button 
              className={`p-2 rounded-md bg-gray-800 border border-gray-700 text-gray-400 hover:bg-gray-700 ${currentPage === 1 ? 'opacity-50 cursor-not-allowed' : ''}`}
              onClick={() => currentPage > 1 && handlePageChange(currentPage - 1)}
              disabled={currentPage === 1}
            >
              <svg className="h-5 w-5" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M15 19l-7-7 7-7" />
              </svg>
            </button>
            
            {/*      */}
            {Array.from({ length: totalPages }, (_, i) => i + 1).map(page => (
              <button 
                key={page}
                onClick={() => handlePageChange(page)}
                className={`px-4 py-2 rounded-md ${
                  currentPage === page 
                  ? "bg-gray-100 text-gray-900 font-medium" 
                  : "bg-gray-800 border border-gray-700 text-gray-300 hover:bg-gray-700"
                }`}
              >
                {page}
              </button>
            ))}
            
            <button 
              className={`p-2 rounded-md bg-gray-800 border border-gray-700 text-gray-400 hover:bg-gray-700 ${currentPage === totalPages ? 'opacity-50 cursor-not-allowed' : ''}`}
              onClick={() => currentPage < totalPages && handlePageChange(currentPage + 1)}
              disabled={currentPage === totalPages}
            >
              <svg className="h-5 w-5" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M9 5l7 7-7 7" />
              </svg>
            </button>
          </nav>
        </div>
      )}
    </div>
  );
}
